/*import Vue from 'vue'
import VueRouter from 'vue-router'*/
import Index from '@/views/index'
import Question from '@/views/question'
Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Index',
        component: Index
    },
    {
        path: '/question',
        name: 'Question',
        component: Question
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
